import React, { useState } from "react";
import styled from "styled-components";
import Header from "./Header";
import ShoppingCart from "./ShoppingCart";
import data from "../data";
import { connect } from "react-redux";
import { addToCart } from "../redux/Shopping/shopping-action";

function Product(props) {
  const [isCartOpen, setIsCartOpen] = useState(false);

  const { products } = data;
  let currentId = props.history.location.hash.slice(-1);
  const currentProduct = products[currentId];

  return (
    <div>
      <>
        <Header open={() => setIsCartOpen(!isCartOpen)} />
        <ProductPage>
          <LeftSection style={{ backgroundColor: currentProduct.background }}>
            <ProductWrapper>
              <img src={currentProduct.image} />
              <ProductDescription id={currentProduct.id}>
                <TextProduct>
                  <p>{currentProduct.description}</p>
                  <h1>{currentProduct.name}</h1>
                  <p>${currentProduct.price}</p>
                </TextProduct>
                <button
                  onClick={(e) =>
                    props.addToCart(e.target.parentNode.getAttribute("id"))
                  }
                >
                  Add to cart
                </button>
              </ProductDescription>
            </ProductWrapper>
          </LeftSection>
          <RightSection>
            <ProductDetailsWrapper>
              <h2>Intro</h2>
              <p>
                Simple, timeless design that’s still going strong after 40 years
                in our stores. It’s loved by all ages so we made a grown-up size
                and a children’s size.
              </p>
              <h2>Product Information</h2>
              <Row1>
                <p>Dimmensions</p>
                <p>52x50x88 cm</p>
              </Row1>
              <Row2>
                <p>Materials</p>
                <ul>
                  <p>Polypropylene plastic</p>
                  <p>Aluminium</p>
                  <p>Solid birch</p>
                </ul>
              </Row2>
              <Row3>
                <p>Good to know</p>
                <p>Wipe clean with a cloth dampened in a mild cleaner.</p>
              </Row3>
            </ProductDetailsWrapper>
          </RightSection>
        </ProductPage>
        {isCartOpen && <ShoppingCart open={() => setIsCartOpen(!isCartOpen)} />}
      </>
    </div>
  );
}

const ProductPage = styled.div`
  display: flex;
  height: 600px;
  position: relative;
  @media (max-width: 1080px) {
    display: block;
  }
`;
const LeftSection = styled.div`
  width: 60%;
  height: 100%;
  @media (max-width: 1080px) {
    width: 100%;
  }
`;
const RightSection = styled.div`
  width: 40%;
  background-color: white;
  height: 100%;
  @media (max-width: 1080px) {
    width: 100%;
  }
`;

const ProductWrapper = styled.div`
  height: 100%;
  display: grid;
  place-items: center;
  img {
    height: 400px;
    @media (max-width: 768px) {
      width: 100%;
      height: auto;
    }
  }
`;

const ProductDescription = styled.div`
  display: flex;
  justify-content: space-between;
  width: 70%;
  button {
    align-content: center;
    margin: 30px 0;
    border: none;
    padding: 0 50px;
    border-radius: 20px;
    cursor: pointer;
  }
  button:hover {
    box-shadow: 0px 2px 8px #00000014;
  }
  @media (max-width: 768px) {
    width: 100%;
    justify-content: space-around;
  }
`;

const TextProduct = styled.div`
  h1 {
    font-size: 35px;
  }
`;

const ProductDetailsWrapper = styled.div`
  padding: 50px;
  h2 {
    line-height: 4;
  }
  p {
    font-size: 14px;
  }
`;
const Row1 = styled.div`
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
  padding-bottom: 25px;
  border-bottom: 1px solid black;
`;
const Row2 = styled.div`
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
  padding-bottom: 25px;
  border-bottom: 1px solid black;
  ul > p {
    display: block;
    text-align: end;
  }
`;
const Row3 = styled.div`
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
  padding-bottom: 25px;
  border-bottom: 1px solid black;
  p:nth-child(2) {
    text-align: end;
  }
`;

const mapDispatchToProps = (dispatch) => {
  return {
    addToCart: (id) => dispatch(addToCart(id)),
  };
};

export default connect(null, mapDispatchToProps)(Product);
