import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { connect } from "react-redux";
import { removeFromCart, adjustQty } from "../redux/Shopping/shopping-action";
import { Link } from "react-router-dom";

function ShoppingCart(props) {
  const [totalPrice, setTotalPrice] = useState(0);
  const [totalItems, setTotalItems] = useState(0);
  const [input, setInput] = useState(1);

  useEffect(() => {
    let items = 0;
    let price = 0;
    props.cartItems.forEach((item) => {
      items += parseInt(item.qty);
      price += parseInt(item.qty * item.price);
    });
    setTotalPrice(price);
    setTotalItems(items);
  }, [props.cartItems, totalItems, totalPrice, setTotalItems, setTotalPrice]);

  const handleChange = (e, itemID) => {
    let value = e.target.value;
    setInput(value);

    props.adjustQty(itemID, value);
  };

  return (
    <div>
      <>
        <ShoppingCartPop>
          <button onClick={props.open}>
            <img src="../../img/close-icon.svg" />
          </button>
          <ShoppingCartWrapper>
            <h1>Shopping cart</h1>
            {props.cartItems.length === 0 && <p>The shopping cart is empty</p>}

            {props.cartItems.length > 0 && (
              <div>
                <Title>
                  <p>Product</p>
                  <p>Quantity</p>
                  <p>U Price</p>
                  {/* Balance the table */}
                  <span></span>
                </Title>
                {props.cartItems.map((item) => {
                  return (
                    <ProductItem key={item.id}>
                      <p>{item.name}</p>
                      <input
                        onChange={(e) => handleChange(e, item.id)}
                        type="number"
                        value={item.qty}
                      />
                      <p>{item.price}</p>
                      <span onClick={() => props.removeFromCart(item.id)}>
                        x
                      </span>
                    </ProductItem>
                  );
                })}
              </div>
            )}
            <TotalLine>
              <p>Total</p>
              {props.cartItems.length > 0 && <p>{totalItems}</p>}
              {props.cartItems.length > 0 && <p>{totalPrice}</p>}
              {/* Balance the table */}
              <span></span>
            </TotalLine>
            <Link to="/thank-you">
              <button>BUY</button>
            </Link>
          </ShoppingCartWrapper>
        </ShoppingCartPop>
      </>
    </div>
  );
}

const ShoppingCartPop = styled.div`
  position: fixed;
  top: 0;
  left: 70%;
  right: 0;
  bottom: 0;
  z-index: 999;
  color: black;
  background-color: white;
  border-left: 1px solid grey;
  @media (max-width: 768px) {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  button:first-child {
    position: absolute;
    right: 0;
    width: 40px;
    height: 40px;
    align-content: left;
    background-color: transparent;
    border: none;
    cursor: pointer;
    img {
      place-items: center;
      width: 20px;
      height: 20px;
    }
  }
  button:last-child {
    position: absolute;
    width: 150px;
    right: 30px;
    align-content: center;
    margin: 30px 0;
    border: none;
    padding: 10px 50px 10px 50px;
    border-radius: 20px;
    cursor: pointer;
    background-color: #add4ff;
  }
  button:hover {
    box-shadow: 0px 2px 8px #00000014;
  }
`;

const ShoppingCartWrapper = styled.div`
  padding: 120px 30px;
`;

const ProductItem = styled.div`
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid grey;
  p,
  input,
  span {
    width: 25%;
    text-align: left;
    margin: 20px 0;
    cursor: pointer;
  }
  input,
  input:active,
  input:focus {
    border-radius: 10px;
    border: 1px solid grey;
    text-align: left;
    padding-left: 20px;
    outline: none;
  }

  button {
    min-width: 20px;
    border-radius: 50%;
    border: none;
  }
  button:hover {
    box-shadow: 0px 2px 8px #00000014;
  }
  span {
    width: 25px;
    height: 25px;
    text-align: center;
    border-radius: 50%;
    justify-content: center;
  }
  span:hover {
    box-shadow: 0px 2px 8px #00000014;
  }
`;

const Title = styled.div`
  display: flex;
  justify-content: space-between;
  p {
    width: 25%;
    text-align: left;
    margin: 20px 0;
  }
  span {
    width: 25px;
    height: 25px;
  }
`;

const TotalLine = styled.div`
  display: flex;
  justify-content: space-between;
  font-weight: 800;
  font-size: 25px;
  p {
    width: 25%;
    text-align: left;
    margin: 20px 0;
  }
  span {
    width: 25px;
    height: 25px;
  }
`;

const mapStateToProps = (state) => {
  return {
    cartItems: state.shop.cart,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    removeFromCart: (id) => dispatch(removeFromCart(id)),
    adjustQty: (id, value) => dispatch(adjustQty(id, value)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ShoppingCart);
