import "./App.css";
import styled from "styled-components";
import Header from "./components/Header";
import { connect } from "react-redux";
import ShoppingCart from "../src/components/ShoppingCart";
import React, { useState } from "react";
import { Link } from "react-router-dom";

function App({ products }) {
  const [isCartOpen, setIsCartOpen] = useState(false);
  return (
    <>
      <div>
        <Header open={() => setIsCartOpen(!isCartOpen)} />
        <Hero>
          <img src={products[7].image} />
          <ContentHero>
            <p>{products[7].description}</p>
            <h1>{products[7].name}</h1>
            <p>${products[7].price}</p>
            <Link to="/product/#=7">
              <button>More</button>
            </Link>
          </ContentHero>
        </Hero>
        <SectionWrapper>
          <GridSection>
            {products.map((product) => {
              return (
                <div
                  className={`area${product.id}`}
                  style={{ gridArea: `area${product.id}` }}
                  key={product.id}
                >
                  <ProductHolder
                    style={{ backgroundColor: `${product.background}` }}
                  >
                    <h1>{product.name}</h1>
                    <img src={product.image} />
                    <Button>
                      <Link to={`/product/#=${product.id - 1}`}>
                        <img src="../img/Ellipse.png" />
                        <p>More</p>
                        <svg
                          width="24"
                          height="24"
                          xmlns="http://www.w3.org/2000/svg"
                          fill-rule="evenodd"
                          clip-rule="evenodd"
                        >
                          <path d="M21.883 12l-7.527 6.235.644.765 9-7.521-9-7.479-.645.764 7.529 6.236h-21.884v1h21.883z" />
                        </svg>
                      </Link>
                    </Button>
                  </ProductHolder>
                </div>
              );
            })}
          </GridSection>
        </SectionWrapper>
        {isCartOpen && <ShoppingCart open={() => setIsCartOpen(!isCartOpen)} />}
      </div>
    </>
  );
}

const Hero = styled.div`
  background-color: #b0d5d8;
  width: 100%;
  height: 700px;
  display: grid;
  place-items: center;
  img {
    height: 550px;
    @media (max-width: 768px) {
      width: 70%;
      height: auto;
    }
  }
  @media (max-width: 768px) {
    width: 100%;
    height: auto;
  }
  position: relative;
`;

const ContentHero = styled.div`
  position: absolute;
  left: 100px;
  bottom: 100px;
  h1 {
    font-size: 35px;
  }
  a {
    display: block;
    text-align: center;
    text-decoration: none;
    color: inherit;
    button {
      margin: 30px 0 0 0;
      border: none;
      padding: 10px 40px;
      border-radius: 20px;
      cursor: pointer;
    }
    button:hover {
      box-shadow: 0px 2px 8px #00000014;
    }
  }
  a,
  a:hover,
  a:focus,
  a:active {
    text-decoration: none;
    color: black;
  }
`;

const SectionWrapper = styled.div`
  margin: 100px;
  @media (max-width: 768px) {
    margin: 0px;
  }
`;
const GridSection = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 15px 15px;
  grid-template-areas:
    "area1 area2 area3"
    "area4 area4 area5"
    "area6 area7 area8";

  @media (max-width: 768px) {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-areas:
      "area1"
      "area2"
      "area3"
      "area4"
      "area5"
      "area6"
      "area7"
      "area8"
      "area9";
  }
`;

const ProductHolder = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 500px;
  padding: 20px 0;
  & > img {
    padding: 40px 0;
    max-height: 300px;
    @media (max-width: 768px) {
      width: auto;
      max-width: 100%;
      height: auto;
    }
  }
`;
const Button = styled.div`
  width: 100%;
  height: 100px;
  align-items: center;
  a {
    margin: 0 auto;
    width: 30%;
    height: 100%;
    display: flex;
    position: relative;
    cursor: pointer;
    img {
      position: absolute;
      height: 50px;
    }
    p {
      position: absolute;
      left: 40px;
      top: 18px;
    }
    svg {
      position: absolute;
      right: 0;
      top: 15px;
    }
  }
  a,
  a:hover,
  a:focus,
  a:active {
    text-decoration: none;
    color: black;
  }
`;

const mapStateToProps = (state) => {
  return {
    products: state.shop.products,
  };
};

export default connect(mapStateToProps)(App);
